<template>
  <div id="base_home">
    <el-container class="home-container">
      <el-header class="home-header">
        <span class="home_title">助学管理系统</span>
        <span class="tip">{{messageText.content}}</span>
        <div style="display: flex;align-items: center;margin-right: 7px">
          <el-dropdown @command="handleCommand">
        <span class="el-dropdown-link home_userinfo" style="display: flex;align-items: center">
          {{userInfo.name}}
        </span>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item command="userInfo" divided>个人中心</el-dropdown-item>
              <el-dropdown-item command="logout" divided>注销</el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </div>
      </el-header>
      <el-container>
        <el-aside width="180px" class="home-aside" style=" background: #324157;">
          <div style="display: flex;justify-content: flex-start;width: 180px;text-align: left;">
            <el-menu style="width: 180px; " min-width="175px" background-color="#324157" text-color="#fff" active-text-color="#ffd04b" unique-opened :default-active="$route.path" router >
              <template v-for="(item,index) in $router.options.routes" v-if="!item.hidden">
                <el-submenu :index="index+''" v-if="!item.isLeaf">
                  <template slot="title">
                  <!--<i class="el-icon-menu"></i>-->
                  <span>{{item.name}}</span>
                  </template>
                  <el-menu-item v-for="(itemChild,index1) in item.children" :index="itemChild.path" :key="itemChild.path">
                    <template slot="title">
                    <!--<i class="el-icon-menu"></i>-->
                    {{itemChild.name}}
                    </template>
                  </el-menu-item>
                </el-submenu>
                <el-menu-item :index=item.children[0].path v-if="item.isLeaf && item.children.length>0 && userInfo.type!==4 " >
                  <template slot="title" >
                    <!--<i class="el-icon-menu"></i>-->
                    {{item.children[0].name}}
                  </template>
                </el-menu-item>
                <el-menu-item :index=item.children[0].path v-if="item.isLeaf && item.children.length>0 && userInfo.type===4 && item.children[0].path!=='/system'" >
                  <template slot="title" >
                    <!--<i class="el-icon-menu"></i>-->
                    {{item.children[0].name}}
                  </template>
                </el-menu-item>

              </template>
            </el-menu>
          </div>
        </el-aside>
        <el-container >
          <el-main >
            <el-breadcrumb separator-class="el-icon-arrow-right">
              <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
              <el-breadcrumb-item v-text="this.$router.currentRoute.name"></el-breadcrumb-item>
            </el-breadcrumb>
            <el-dialog title="个人中心" :visible.sync="userInfoVisible" style="width: auto;margin: auto" >
              <el-form :model="userForm" label-width="80px">
                <el-form-item label="密码" :label-width="formLabelWidth">
                  <el-input v-model="userForm.password" placeholder="密码"></el-input>
                </el-form-item>

                <el-form-item label="电话" :label-width="formLabelWidth">
                  <el-input v-model="userForm.phone" placeholder="请输入电话"></el-input>
                </el-form-item>
                <el-form-item label="家庭住址" :label-width="formLabelWidth">
                  <el-input v-model="userForm.address" placeholder="家庭住址"></el-input>
                </el-form-item>

              </el-form>
              <div slot="footer" class="dialog-footer">
                <el-button @click="userInfoVisible = false">取 消</el-button>
                <el-button type="primary" @click="addUserInfo">确 定</el-button>
              </div>
            </el-dialog>
            <router-view/>
          </el-main>
        </el-container>
      </el-container>
    </el-container>
  </div>
</template>
<script>
  export default{
    created: function () {
//      this.devMsg();
//       this.loadNF();
      this.getMessage();
      //轮询展示消息
      let i=0;
      setInterval(()=>{
        if (i<this.messageData.length){
          this.messageText.content='系统提示:'+this.messageData[i].content
          i++;
        }  else {
          i=0;
        }
      },3000)
    },
    methods: {
      handleAvatarSuccess(res, file) {
        this.userForm.headImg = res.data;
      },
      beforeAvatarUpload(file) {
        const isLt2M = file.size / 1024 / 1024 < 5;
        if (!isLt2M) {
          this.$message.error('上传头像图片大小不能超过 5MB!');
        }
        return  isLt2M;
      },
      addUserInfo(){
        let _this = this;
        let obj = _this.userForm;

        obj.account = this.userInfo.account;
        obj.id =  this.userInfo.id;
        this.putRequest('/user',JSON.stringify(_this.userForm)).then((req) =>{
          this.$message({
            message:'更新成功',
            type:'success',
          });
          this.$store.state.user =req.data.data;
        }).catch(()=>{
          this.$message({
            message:'更新失败',
            type:'error',
          })
        });
        _this.userInfoVisible=false;
      },
      handleCommand(cmd){
        var _this = this;
        if (cmd == 'logout') {
          this.$confirm('注销登录, 是否继续?', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
          }).then(() => {
            console.log("退出")
            // _this.getRequest("/logout");
            _this.$store.commit('logout');
            console.log("退出")
            _this.$router.replace({path: '/'});
          }).catch(() => {
            _this.$message({
              type: 'info',
              message: '取消'
            });
          });
        }
        if (cmd == 'userInfo') {
          _this.userInfoVisible = true
        }
      },


      //轮询获取系统最新消息
      getMessage(){
        setInterval(() => {
          this.getRequest('/message').then((req)=>{
            this.messageData = req.data.data;
          })
          // this.getMessage();
        }, 5000);

      },
    },
    data(){
      return {
        isDot: false,
        userInfoVisible:false,
        formLabelWidth:'80px',
        messageText:{
          id:'',
          content:''
        },
        messageData:[],
        userForm:{
          account:'',
          name:'',
          password:'',
          type:'',
          address:'',
          age:0,
          headImg:'',
          phone:'',
          id:''
        },
      }
    },
    computed: {
      userInfo(){
        return this.$store.state.user;
      },
    }
  }
</script>
<style>
  .home-container {
    height: 100%;
    position: absolute;
    top: 0px;
    left: 0px;
    width: 100%;
  }

  .home-header {
    background-color: #76878e;
    color: #333;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: space-between;
    box-sizing: content-box;
    padding: 0px;
  }

  .home-aside {
    background-color: #ECECEC;
  }

  .home-main {
    background-color: #fff;
    color: #000;
    text-align: center;
    margin: 0px;
    padding: 0px;;
  }

  .home_title {
    color: #fff;
    font-size: 22px;
    display: inline;
    margin-left: 8px;
  }
  .tip{
    color: #ffd168;
    font-size: 10px;
    display: inline;
    margin-left: 0px;
  }

  .home_userinfo {
    color: #fff;
    cursor: pointer;
  }

  .home_userinfoContainer {
    display: inline;
    margin-right: 20px;
  }

  .el-submenu .el-menu-item {
    width: 180px;
    min-width: 175px;
  }
  .avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 100px;
    height: 100px;
    line-height: 100px;
    text-align: center;
  }
  .avatar {
    width: 178px;
    height: 178px;
    display: block;
  }
</style>



