<template>
  <div>
    <el-col :span="24" class="messageToolbar" style="padding-bottom: 0px;">
      <el-button type="primary" @click="handleMessage">发布公告</el-button>
      <el-button  type="primary" @click="exportExcel">导出报表</el-button>
    </el-col>

    <el-table :data="messageData" highlight-current-row v-loading="listLoading" style="width: 100%">
      <el-table-column type="index" width="60">
      </el-table-column>
      <el-table-column prop="createDate" label="发布时间"  sortable></el-table-column>
      <el-table-column prop="content" label="内容"  sortable></el-table-column>
      <el-table-column prop="userName" label="发布人"  sortable></el-table-column>
    </el-table>
    <el-dialog title="发布系统消息" label-width="80px" :visible.sync="addMessageVisible">
      <el-form :model="MessageForm"  ref="MessageForm">

        <el-form-item label="内容" :label-width="formLabelWidth" prop="content">
          <el-input type="textarea" v-model="MessageForm.content" placeholder="内容" style="width: 400px"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="addMessageVisible = false">取 消</el-button>
        <el-button type="primary" @click="addMessage">确 定</el-button>
      </div>
    </el-dialog>

  </div>
</template>
<script>
  export default {
    mounted: function () {
      this.getMessage()
    },

    methods:{
      handleMessage(){
        this.addMessageVisible=true;
        this.getUsers();
      },
      getMessage(){
        this.getRequest('/message').then((req)=>{
          this.messageData = req.data.data;
        })
      },
      getUsers(){
        this.postRequest('/user/condition',JSON.stringify(this.queryUserForm)).then((req)=>{
          this.userDate = req.data.data;

        })
      },
      exportExcel(){
        window.open('http://localhost:8081/position/export');
      },
      addMessage(){
        this.MessageForm.userId = this.$store.state.user.id
        this.postRequest('/message', this.MessageForm).then((req) =>{
          this.$message({
            message:'发布成功',
            type:'success'
          });
          this.addMessageVisible = false;
          this.getMessage();
        })
      },

    },
    data(){
      return{
        addMessageVisible:false,
        listLoading:false,
        formLabelWidth:'120px',
        messageData:[],
        MessageForm:{
         userId:'',
          userName:'',
          content:'',
        },
        userDate:[],
        queryUserForm:{
          name:'',
          position:'',
          deptId:'',
        },
      }

    },
  }
</script>
<style>
  .messageToolbar {
    background: #ffffff;
    padding: 10px;
  / / border: 1 px solid #dfe6ec;
    margin: 0px 10px;
  }
</style>



