<template>
  <div>
    <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
      <el-form :inline="true" :model="queryUserForm"   class="selectForm" >
        <el-form-item label="姓名">
          <el-input v-model="queryUserForm.name" placeholder="姓名"></el-input>
        </el-form-item>
        <!--<el-form-item label="部门"  prop="deptManager">-->
          <!--<el-select   v-model="queryUserForm.deptId" filterable placeholder="部门" style="width: 400px" clearable prop="deptId">-->
            <!--<el-option  v-for="item in deptData"  :key="item.id" :label="item.name" :value="item.id.toString()" ></el-option>-->
          <!--</el-select>-->
        <!--</el-form-item>-->
        <el-form-item>
          <el-button type="primary" @click="getUsers">查询</el-button>
        </el-form-item>
        <!--<el-button type="primary" @click="openAddUserDialog">新增</el-button>-->
      </el-form>
    </el-col>
    <el-table :data="tableData" highlight-current-row  style="width: 100%" border>
      <el-table-column type="index" width="60">
      </el-table-column>
      <el-table-column prop="name" label="姓名" width="180" sortable></el-table-column>
      <el-table-column prop="idCard" label="身份证号" width="180" sortable></el-table-column>
      <el-table-column prop="phone" label="电话" sortable></el-table-column>
      <el-table-column prop="address" label="家庭住址" sortable></el-table-column>

      <el-table-column prop="deptName" label="职位" sortable></el-table-column>
      <el-table-column prop="positionName" label="岗位" sortable></el-table-column>
      <el-table-column fixed="right" label="操作" width="180">
        <template slot-scope="scope" >
          <el-button  type="primary " size="small" @click="handleUserEdit(scope.$index, scope.row)">排班</el-button>
          <el-button  type="primary " size="small" @click="queryWorkPlan(scope.$index, scope.row)">查看</el-button>
        </template>
      </el-table-column>
    </el-table>

    <el-dialog title="排班" label-width="100px" :visible.sync="workVisible">
      <span class="demonstration">默认</span>
      <el-date-picker
        v-model="temp"
        type="daterange"
        range-separator="至"
        start-placeholder="开始日期"
        end-placeholder="结束日期">
      </el-date-picker>
      <el-button  type="primary " size="small" @click="addWork">提交</el-button>
    </el-dialog>
<el-dialog label-width="100px" :visible.sync="workPlanVisible">
  <el-table :data="tableData"    >
    <!--<el-table-column type="index" width="60">-->
    <!--</el-table-column>-->
    <el-table-column prop="startDate" label="开始时间"  sortable></el-table-column>
    <el-table-column prop="endDate" label="结束时间"  sortable></el-table-column>
  </el-table>
</el-dialog>


    <!--<router-view/>-->
  </div>
</template>
<script>
  export default{
    mounted: function () {
      this.getUsers();
      // this.getDept();
    },
    methods:{
      handleUserEdit(index, row){
        this.workVisible = true;
        this.editUserForm = Object.assign({}, row);
        // this.getPosition(this.editUserForm.deptId)

      },
      queryWorkPlan(){
        this.getRequest('/signed/work?userId='+this.$store.state.user.id).then((req) => {
          this.tableData = req.data.data
          this.workPlanVisible=true;
        })

      },
      getUsers(){
        this.postRequest('/user/condition',JSON.stringify(this.queryUserForm)).then((req)=>{
          this.tableData = req.data.data;
          this.tableData.push();
          this.$message({
            message:'查询成功',
            type:'success'
          })
        })
      },

      addWork(){
        console.log(JSON.stringify(this.temp[0]))
        this.work.userId =this.$store.state.user.id;
        this.work.startDate = this.temp[0];
        this.work.endDate = this.temp[1];
        this.postRequest('/signed/work',JSON.stringify(this.work)).then((req) => {
          this.$message({
            message:'排班成功',
            type:'success'
          })
          this.workVisible = false;
        })
      }
    },
    data(){
      return {
        workVisible:false,
        workPlanVisible:false,
        formLabelWidth:'120px',
        queryUserForm:{
          name:'',
          position:'',
          deptId:'',
        },
       work:{
         userId:'',
         startDate:'',
         endDate:''
       },

        tableData:[],
        deptData:[],
        positionData:[],
        temp:''
      }
    }
  }
</script>
<style>
  .toolbar {
    background: #f2f2f2;
    padding: 10px;
    /*/ / border: 1 px solid #dfe6ec;*/
    margin: 10px 0px;
  }
</style>



