<template>
  <div>
    <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
      <el-form :inline="true" :model="queryDeptForm"   class="selectForm" >
        <el-form-item label="部门名称">
          <el-input v-model="queryDeptForm.name" placeholder="部门名称"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="getDept">查询</el-button>
          <el-button type="primary" @click="addDeptVisiable = true">新增</el-button>
        </el-form-item>
      </el-form>
    </el-col>
    <el-table :data="deptData" highlight-current-row v-loading="listLoading" style="width: 100%" >
      <el-table-column type="index" width="60">
      </el-table-column>
      <el-table-column prop="name" label="部门名"  sortable></el-table-column>
      <el-table-column prop="manager" label="主管"  sortable></el-table-column>

      <el-table-column fixed="right" label="操作" width="150">
        <template slot-scope="scope" >
          <el-button  size="small" @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
          <el-button type="danger"  size="small" @click="handleDel(scope.$index, scope.row)">注销</el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-dialog title="新增" label-width="80px" :visible.sync="addDeptVisiable">
      <el-form :model="addDeptForm"  ref="addDeptForm">

        <el-form-item label="部门名称" :label-width="formLabelWidth" prop="name">
          <el-input v-model="addDeptForm.name" placeholder="院系名" style="width: 400px"></el-input>
        </el-form-item>
        <el-form-item label="管理员" :label-width="formLabelWidth" prop="deptManager">
          <el-input v-model="addDeptForm.manager" placeholder="管理员" style="width: 400px"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="addDeptVisiable = false">取 消</el-button>
        <el-button type="primary" @click="addDept">确 定</el-button>
      </div>
    </el-dialog>
    <el-dialog title="编辑" label-width="80px" :visible.sync="editDeptVisiable">
      <el-form :model="deptForm"  ref="deptForm">
        <el-form-item label="部门名" :label-width="formLabelWidth" prop="name">
          <el-input v-model="deptForm.name" placeholder="部门" style="width: 400px"></el-input>
        </el-form-item>
        <el-form-item label="管理员" :label-width="formLabelWidth" prop="deptManager">
          <el-input v-model="deptForm.manager" placeholder="管理员" style="width: 400px"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="editDeptVisiable = false">取 消</el-button>
        <el-button type="primary" @click="updateDept">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
  export default {
    mounted: function () {
      let _this = this;
      _this.getDept();
    },
    methods:{
      getDept(){
        this.postRequest('/dept/condition',JSON.stringify(this.queryDeptForm)).then((req)=>{
          debugger
          this.deptData = req.data.data
          this.$message({
            type:'success',
            message:'查询成功'
          })
        }).catch(()=>{
          this.$message({
            type:'error',
            message:'查询失败'
          })
        })
      },
      updateDept(){
        let _this=this;

        _this.putRequest('/dept', JSON.stringify(this.deptForm)).then((data) => {
          this.$message({
            type:'success',
            message:'提交成功'
          })
          _this.editDeptVisiable=false;
          this.getDept();
        }).catch((req)=>{
          debugger
          this.$message({
            type:'error',
            message:req.response.data.message
          })
        })
      },
      addDept(){
        let _this=this;
        _this.postRequest('/dept', JSON.stringify(this.addDeptForm)).then((data) => {
          this.$message({
            type:'success',
            message:'提交成功'
          })
          _this.addDeptVisiable=false;
          _this.getDept();
        }).catch((req)=>{
          debugger
          this.$message({
            type:'error',
            message:req.response.data.message
          })
        })
      },

      handleEdit(index, row){
        this.editDeptVisiable = true;
        this.deptForm = Object.assign({}, row);
      },
      handleDel(index, row){
        this.$confirm('确认删除该记录吗?', '提示', {
          type: 'warning'
        }).then(() => {
          let _this = this;
          //NProgress.start();
          this.deleteRequest('/dept/'+row.id).then((res)=>{
            this.$message({
              message: '删除成功',
              type: 'success'
            });
            this.getDept();
          }).catch(()=>{
            this.$message({
              message: '删除失败',
              type: 'error'
            });
          })
        })
      }
    },
    data(){
      return{
        listLoading:false,
        editDeptVisiable:false,
        addDeptVisiable:false,
        formLabelWidth:'120px',
        deptRule: {
          name:[
            { required: true, message: '请输入院系名', trigger: 'blur' },
          ]},
        deptForm:{
          name:'',
          manager:''
        },
        addDeptForm:{
          name:'',
          manager:''
        },
        queryDeptForm:{
          name:'',
        },
        deptData:[],

      }

    },
  }
</script>
<style>
  .toolbar {
    background: #f2f2f2;
    padding: 10px;
  / / border: 1 px solid #dfe6ec;
    margin: 10px 0;
  }
</style>



