<template>
  <div>
    <el-col :span="24" class="commentsToolbar" style="padding-bottom: 0px;">
      <el-button type="primary" @click="handleComment">新增评价</el-button>
    </el-col>
    <el-table :data="commentsData" highlight-current-row v-loading="listLoading" style="width: 100%" >
      <el-table-column type="index" width="60">
      </el-table-column>
      <el-table-column prop="createDate" label="评价时间"  sortable></el-table-column>
      <el-table-column prop="content" label="内容"  sortable></el-table-column>
      <el-table-column prop="publishUserName" label="评论人"  sortable></el-table-column>
    </el-table>
    <el-dialog title="新增评价" label-width="80px" :visible.sync="addCommentsVisible">
      <el-form :model="commentsForm"  ref="commentsForm">
        <el-form-item label="被点评人员" :label-width="formLabelWidth" prop="userName">
          <el-select   v-model="commentsForm.receiveUserId" filterable placeholder="姓名" style="width: 400px" clearable prop="deptId">
            <el-option  v-for="item in userDate"  :key="item.id" :label="item.name" :value="item.id.toString()" ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="内容" :label-width="formLabelWidth" prop="content">
          <el-input type="textarea" v-model="commentsForm.content" placeholder="内容" style="width: 400px"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="addCommentsVisible = false">取 消</el-button>
        <el-button type="primary" @click="addComments">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
  export default {
    mounted: function () {
      this.getComments()
    },

    methods:{
      handleComment(){
        this.addCommentsVisible=true;
        this.getUsers();
      },
      getComments(){
        this.getRequest('/comments/'+this.$store.state.user.id).then((req)=>{
          this.commentsData = req.data.data;
        })
      },
      getUsers(){
        this.postRequest('/user/condition',JSON.stringify(this.queryUserForm)).then((req)=>{
          this.userDate = req.data.data;

        })
      },
      addComments(){
        this.commentsForm.publishUserId = this.$store.state.user.id
        this.postRequest('/comments', this.commentsForm).then((req) =>{
          this.$message({
            message:'评价成功',
            type:'success'
          });
          this.addCommentsVisible = false;
          this.getComments();
        })
      },

    },
    data(){
      return{
        addCommentsVisible:false,
        listLoading:false,
        formLabelWidth:'120px',
        commentsData:[],
        commentsForm:{
          receiveUserId:'',
          publishUserId:'',
          userName:'',
          content:'',
          time:''
        },
        userDate:[],
        queryUserForm:{
          name:'',
          position:'',
          deptId:'',
        },
      }

    },
  }
</script>
<style>
  .commentsToolbar {
    background: #ffffff;
    padding: 10px;
  / / border: 1 px solid #dfe6ec;
    margin: 10px 0;
  }
</style>



