import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import Login from '@/components/Login'
import UserManager from '@/components/UserManager'
import Dept from '@/components/Dept'
import Position from '@/components/Position'
import Approval from '@/components/Approval'
import Work from '@/components/Work'
import Signed from '@/components/Signed'
import coments from '@/components/coments'
import Message from '@/components/Message'
import Salary from '@/components/Salary'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/login',
      hidden:true
    },
    {
      path: '/login',
      name: 'Login',
      component: Login,
      hidden:true
    },
    {
      path:'/home',
      name:'首页',
      component:Home,
      hidden:true
    },
    {
      path: '/Home',
      name: '',
      component: Home,
      isLeaf: true,
      children: [
        {
          path: '/userManager', component: UserManager, name: '人员管理',
        }]
    },
    {
      path: '/Home',
      name: '',
      component: Home,
      isLeaf: true,
      children: [
        {
          path: '/dept', component: Dept, name: '部门管理',
        }]
    },
    {
      path: '/Home',
      name: '',
      component: Home,
      isLeaf: true,
      children: [
        {
          path: '/position', component: Position, name: '岗位管理',
        }]
    },
    {
      path: '/Home',
      name: '',
      component: Home,
      isLeaf: true,
      children: [
        {
          path: '/approval', component: Approval, name: '审批管理',
        }]
    },
    {
      path: '/Home',
      name: '考勤管理',
      component: Home,
      // isLeaf: true,
      children: [
        {
          path: '/signed', component: Signed, name: '签到管理',
        },
        {
          path: '/work', component: Work, name: '排班管理',
        },
        ]
    },
    {
      path: '/Home',
      name: '',
      component: Home,
      isLeaf: true,
      children: [
        {
          path: '/coments', component: coments, name: '评价管理',
        }]
    },
    {
      path: '/Home',
      name: '',
      component: Home,
      isLeaf: true,
      children: [
        {
          path: '/message', component: Message, name: '系统公告',
        }]
    },
    {
      path: '/Home',
      name: '',
      component: Home,
      isLeaf: true,
      children: [
        {
          path: '/salary', component: Salary, name: '工资管理',
        }]
    },

  ]
})



