<template>
  <div>
    <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
      <el-form :inline="true" :model="queryForm"   class="selectForm" >
        <el-form-item label="岗位名">
          <el-input v-model="queryForm.name" placeholder="岗位"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="queryPosition">查询</el-button>
          <el-button type="primary" @click="openAddPosition">发布新岗位</el-button>
        </el-form-item>
      </el-form>
    </el-col>
    <el-table :data="positionData" highlight-current-row  style="width: 100%" >
      <el-table-column type="index" width="60">
      </el-table-column>
      <el-table-column prop="name" label="岗位名"  sortable></el-table-column>
      <el-table-column prop="deptName" label="所属部门"  sortable></el-table-column>
      <el-table-column prop="deptManager" label="部门负责人"  sortable></el-table-column>
      <el-table-column fixed="right" label="操作" width="150">
        <template slot-scope="scope" >
          <el-button  size="small" @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
          <el-button type="danger"  size="small" @click="handleDel(scope.$index, scope.row)">撤销</el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-dialog title="新增" label-width="80px" :visible.sync="addPositionVisiable">
      <el-form :model="addPositionForm"  ref="addPositionForm">
        <el-form-item label="岗位名" :label-width="formLabelWidth" prop="deptName">
          <el-input v-model="addPositionForm.name" placeholder="岗位名" style="width: 400px"></el-input>
        </el-form-item>
        <el-form-item label="部门" :label-width="formLabelWidth" prop="deptManager">
          <!--<el-input v-model="addPositionForm.dept" placeholder="管理员" style="width: 400px"></el-input>-->
          <el-select   v-model="addPositionForm.deptId" filterable placeholder="部门" style="width: 400px" clearable prop="deptId">
            <el-option  v-for="item in deptData"  :key="item.id" :label="item.name" :value="item.id.toString()" ></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="addPositionVisiable = false">取 消</el-button>
        <el-button type="primary" @click="addPosition">确 定</el-button>
      </div>
    </el-dialog>
    <el-dialog title="编辑" label-width="80px" :visible.sync="editDeptVisiable">
      <el-form :model="editPositionForm"  ref="editPositionForm">
        <el-form-item label="岗位名" :label-width="formLabelWidth" prop="deptName">
          <el-input v-model="editPositionForm.name" placeholder="岗位名" style="width: 400px"></el-input>
        </el-form-item>
        <el-form-item label="部门" :label-width="formLabelWidth" prop="deptManager">
          <!--<el-input v-model="addPositionForm.dept" placeholder="管理员" style="width: 400px"></el-input>-->
          <el-select   v-model="editPositionForm.deptId" filterable placeholder="部门" style="width: 400px" clearable prop="deptId">
            <el-option  v-for="item in deptData"  :key="item.id" :label="item.name" :value="item.id" ></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="editDeptVisiable = false">取 消</el-button>
        <el-button type="primary" @click="updatePosition">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
  export default {
    mounted: function () {
      let _this = this;
      _this.queryPosition();
      this.getDept();
    },
    methods:{
      openAddPosition(){
        this.addPositionVisiable = true

      },
      getDept(){
        this.postRequest('/dept/condition').then((req)=>{
          this.deptData = req.data.data
        })
      },
      queryPosition(){
          this.postRequest('/position/condition',JSON.stringify(this.queryForm)).then((req) =>{
            this.positionData = req.data.data;
            this.$message({
              message:'查询成功',
              type:'success',
            })
          }).catch(()=>{
            this.$message({
              message:'查询失败',
              type:'error',
            })
          })
      },
      updatePosition(){
        let _this=this;

        _this.putRequest('/position', JSON.stringify(this.editPositionForm)).then((data) => {
          this.$message({
            type:'success',
            message:'提交成功'
          })
          _this.editDeptVisiable=false;
          this.queryPosition();
        }).catch((req)=>{
          this.$message({
            type:'error',
            message:req.response.data.message
          })
        })
      },
      addPosition(){
        let _this=this;
        _this.postRequest('/position', JSON.stringify(this.addPositionForm)).then((data) => {
          this.$message({
            type:'success',
            message:'提交成功'
          })
          _this.addPositionVisiable=false;
          _this.queryPosition();
        }).catch((req)=>{
          debugger
          this.$message({
            type:'error',
            message:req.response.data.message
          })
        })
      },

      handleEdit(index, row){
        this.editDeptVisiable = true;
        this.editPositionForm = Object.assign({}, row);
      },
      handleDel(index, row){
        this.$confirm('确认删除该记录吗?', '提示', {
          type: 'warning'
        }).then(() => {
          let _this = this;
          //NProgress.start();
          this.deleteRequest('/position/'+row.id).then((res)=>{
            this.$message({
              message: '删除成功',
              type: 'success'
            });
            this.queryPosition();
          }).catch(()=>{
            this.$message({
              message: '删除失败',
              type: 'error'
            });
          })
        })
      }
    },
    data(){
      return{
        listLoading:false,
        editDeptVisiable:false,
        addPositionVisiable:false,

        formLabelWidth:'120px',
        deptRule: {
          deptName:[
            { required: true, message: '请输入院系名', trigger: 'blur' },
          ]},
        deptForm:{
          deptName:'',
          deptManager:''
        },
        addPositionForm:{
          name:'',
          deptName:'',
          deptId:''
        },
        editPositionForm:{
          name:'',
          deptName:'',
          deptId:''
        },
        queryForm:{
          name:'',
        },
        deptData:[],
        positionData:[],

      }

    },
  }
</script>
<style>
  .toolbar {
    background: #f2f2f2;
    padding: 10px;
  / / border: 1 px solid #dfe6ec;
    margin: 10px 0;
  }
</style>



