<template>
  <div>
    <!--<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">-->
     <!--<span>待审批列表</span>-->
    <!--</el-col>-->
    <el-table :data="approvalData" highlight-current-row v-loading="listLoading" style="width: 100%" >
      <el-table-column type="index" width="60">
      </el-table-column>
      <el-table-column prop="type" label="申请类型"  sortable></el-table-column>
      <el-table-column prop="userName" label="申请人"  sortable></el-table-column>
      <el-table-column fixed="right" label="操作" width="150">
        <template slot-scope="scope" >
          <el-button  size="small" @click="handleEdit(scope.$index, scope.row)">同意</el-button>
          <el-button type="danger"  size="small" @click="handleDel(scope.$index, scope.row)">拒绝</el-button>
        </template>
      </el-table-column>
    </el-table>

  </div>
</template>
<script>
  export default {
    mounted: function () {
      let _this = this;
      _this.getList();
    },
    methods:{
      getList(){
        this.getRequest('/approval').then((req) => {
          this.approvalData = req.data.data;
          console.log(this.approvalData);
        })
      },


      handleEdit(index, row){
        this.temp = Object.assign({}, row);
        this.postRequest('/approval', this.temp).then((req) => {
          this.$message({
            message:'操作成功',
            type:'success'
          });
          this.getList();
        })
      },
      handleDel(index, row) {
        this.temp = Object.assign({}, row);
        this.postRequest('/approval/remove', this.temp).then((req) => {
          this.$message({
            message:'操作成功',
            type:'success'
          });
          this.getList();
        })
      }
    },
    data(){
      return{
        listLoading:false,

        approvalData:[],
        temp:{
          userId:'',
          position:'',
          type:''
        },
      }

    },
  }
</script>
<style>
  .toolbar {
    background: #f2f2f2;
    padding: 10px;
  / / border: 1 px solid #dfe6ec;
    margin: 10px 0;
  }
</style>



