<template>
  <div>
    <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
    <el-form :inline="true" :model="querySalaryForm"   class="selectForm" >
      <el-form-item label="姓名">
        <el-input v-model="querySalaryForm.userName" placeholder="姓名"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="querySalary">查询</el-button>
        <el-button type="primary" @click="openAddSalary">新增</el-button>
      </el-form-item>
    </el-form>
    </el-col>
    <el-table :data="salaryData" highlight-current-row v-loading="listLoading" style="width: 100%" >
      <el-table-column type="index" width="60">
      </el-table-column>
      <el-table-column prop="userName" label="姓名"  sortable></el-table-column>
      <el-table-column prop="baseSalary" label="基础工资"  sortable></el-table-column>
      <el-table-column prop="performanceSalary" label="绩效工资"  sortable></el-table-column>
      <el-table-column prop="takeOff" label="社保扣除"  sortable></el-table-column>
      <el-table-column prop="total" label="实发金额"  sortable></el-table-column>
      <el-table-column prop="createDate" label="实发时间"  sortable></el-table-column>
    </el-table>
    <el-dialog title="新增" label-width="80px" :visible.sync="addSalaryVisiable">
      <el-form :model="salaryForm"  ref="salaryForm">

        <el-form-item label="姓名" :label-width="formLabelWidth" prop="deptManager">
          <el-select   v-model="salaryForm.userId" filterable placeholder="姓名" style="width: 400px" clearable prop="deptId">
            <el-option  v-for="item in userData"  :key="item.id" :label="item.name" :value="item.id.toString()" ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="基础工资" :label-width="formLabelWidth" prop="deptName">
          <el-input v-model="salaryForm.baseSalary" placeholder="基础工资" style="width: 400px"></el-input>
        </el-form-item>
        <el-form-item label="绩效工资" :label-width="formLabelWidth" prop="deptName">
          <el-input v-model="salaryForm.performanceSalary" placeholder="绩效工资" style="width: 400px"></el-input>
        </el-form-item>
        <el-form-item label="扣除" :label-width="formLabelWidth" prop="deptName">
          <el-input v-model="salaryForm.takeOff" placeholder="扣除" style="width: 400px"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="addSalaryVisiable = false">取 消</el-button>
        <el-button type="primary" @click="addSalary">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
  export default {
    mounted: function () {
      this.getUsers();
    },
    methods:{
      openAddSalary(){
        this.addSalaryVisiable = true;
      },
      getUsers(){
        this.postRequest('/user/condition',JSON.stringify(this.queryUserForm)).then((req)=>{
          this.userData = req.data.data;
        })
      },
      addSalary(){
        this.postRequest('/salary', this.salaryForm).then((req) =>{
          this.$message({
            message:'成功',
            type:'success'
          })
          this.addSalaryVisiable = false;
        }).catch(()=>{
          this.$message({
            message:'请输入正确数字',
            type:'error'
          })
        })
      },
      querySalary(){
        this.postRequest('/salary/condition', this.querySalaryForm).then((req) =>{
          this.$message({
            message:'查询成功',
            type:'success'
          })
          this.salaryData = req.data.data
        })
      }
    },
    data(){
      return{
        formLabelWidth:'120px',
        listLoading:false,
        addSalaryVisiable:false,
        userData:[],
        querySalaryForm:{
          name:'',
        },
        queryUserForm:{
          name:''
        },
        salaryForm:{
          name:'',
          baseSalary:'',
          performanceSalary:'',
          takeOff:'',
          total:'',
          createDate:''
        },
        salaryData:[],
        temp:{
          userId:'',
          position:'',
          type:''
        },
      }

    },
  }
</script>
<style>
  .toolbar {
    background: #eaeaea;
    padding: 10px;
  / / border: 1 px solid #dfe6ec;
    margin: 10px 0;
  }
</style>



