<template>
  <div>
    <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
      <el-button type="primary" @click="signed">签到</el-button>
    </el-col>
    <el-table :data="signedData" highlight-current-row v-loading="listLoading" style="width: 100%" >
      <el-table-column type="index" width="60">
      </el-table-column>
      <el-table-column prop="time" label="签到时间"  sortable></el-table-column>
      <el-table-column prop="userName" label="签到人"  sortable></el-table-column>
      <!--<el-table-column fixed="right" label="操作" width="150">-->
      <!--<template slot-scope="scope" >-->
      <!--<el-button  size="small" @click="handleEdit(scope.$index, scope.row)">同意</el-button>-->
      <!--</template>-->
      <!--</el-table-column>-->
    </el-table>

  </div>
</template>
<script>
  export default {
    mounted: function () {
      this.getSigned()
    },
    methods:{

      signed(){
        this.postRequest('/signed?userId='+this.$store.state.user.id).then((req) =>{
          this.$message({
            message:'签到成功',
            type:'success'
          })
        })
      },
      getSigned(){
        this.getRequest('/signed?userId='+this.$store.state.user.id).then((req) =>{
          this.$message({
            message:'查询成功',
            type:'success'
          })
          this.signedData = req.data.data
        })
      }
    },
    data(){
      return{
        listLoading:false,
        signedData:[],
        temp:{
          userId:'',
          position:'',
          type:''
        },
      }

    },
  }
</script>
<style>
  .toolbar {
    background: #ffffff;
    padding: 10px;
  / / border: 1 px solid #dfe6ec;
    margin: 10px 0;
  }
</style>



