<template>
<div class="login_page fillcontain">
  <el-form :rules="rules" class="login-container" label-position="left"
           label-width="0px" v-loading="loading">
    <h3 class="login_title">系统登录</h3>
    <el-form-item prop="account">
      <el-input type="text" v-model="loginForm.account" auto-complete="off" placeholder="账号"></el-input>
    </el-form-item>
    <el-form-item prop="checkPass">
      <el-input type="password" v-model="loginForm.password" auto-complete="off" placeholder="密码"></el-input>
    </el-form-item>
    <el-form-item style="width: 100%">
      <el-button type="primary" @click.native.prevent="submitClick" style="width: 100%">登录</el-button>
    </el-form-item>
  </el-form>
</div>
</template>
<script>
  export default{
    data(){
      return {
        rules: {
          // account: [{required: true, message: '请输入用户名', trigger: 'blur'}],
          // checkPass: [{required: true, message: '请输入密码', trigger: 'blur'}]
        },
        checked: true,
        loginForm: {
          account: 'admin',
          password: '123456'
        },
        loading: false
      }
    },
    methods: {
      submitClick: function () {
        var _this = this;
        let path = _this.$route.query.redirect;
        // _this.$router.replace({path: path == '/' || path == undefined ? '/userManager' : path});
        this.postRequest('/user/login', JSON.stringify(this.loginForm)).then((req)=>{
          debugger
          if (req.data.meta.success){
            this.$store.commit('login', req.data.data);
            console.log(req);
            let path = _this.$route.query.redirect;
            _this.$router.replace({path: path == '/' || path == undefined ? '/home' : path});
          } else {
            this.$message({
              message:req.data.meta.message,
              type:'warning',
            })
          }

        }).catch(()=>{
          this.$message({
            message:'服务器内部错误',
            type:'error',
          })
        })

      }
    }
  }
</script>
<style>
  .login_page{
    position: absolute;
    width: 100%;
    height: 100%;
    background: url(../assets/login.jpg) no-repeat center center;
    background-size: cover;
    /*background: #0f110c;*/
    /*background-position: center center;*/
    /*background-attachment: fixed*/
  }
  .login-container {
    border-radius: 15px;
    background-clip: padding-box;
    margin: 100px auto;
    width: 350px;
    padding: 35px 35px 15px 35px;
    background: #fff;
    border: 1px solid #eaeaea;
    box-shadow: 0 0 25px #cac6c6;
  }

  .login_title {
    margin: 0px auto 40px auto;
    text-align: center;
    color: #505458;
  }

  .login_remember {
    margin: 0px 0px 35px 0px;
    text-align: left;
  }

</style>



