<template>
  <div>
    <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
      <el-form :inline="true" :model="queryUserForm"   class="selectForm" >
        <el-form-item label="姓名">
          <el-input v-model="queryUserForm.name" placeholder="姓名"></el-input>
        </el-form-item>
          <el-form-item label="部门"  prop="deptManager">
            <el-select   v-model="queryUserForm.deptId" filterable placeholder="部门" style="width: 400px" clearable prop="deptId">
              <el-option  v-for="item in deptData"  :key="item.id" :label="item.name" :value="item.id.toString()" ></el-option>
            </el-select>
          </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="getUsers">查询</el-button>
        </el-form-item>
        <el-button type="primary" @click="openAddUserDialog">新增</el-button>
      </el-form>
    </el-col>
    <el-table :data="tableData" highlight-current-row  style="width: 100%" border>
      <el-table-column type="index" width="60">
      </el-table-column>
      <el-table-column prop="name" label="姓名" width="180" sortable></el-table-column>
      <el-table-column prop="idCard" label="身份证号" width="180" sortable></el-table-column>
      <el-table-column prop="phone" label="电话" sortable></el-table-column>
      <el-table-column prop="address" label="家庭住址" sortable></el-table-column>

      <el-table-column prop="deptName" label="职位" sortable></el-table-column>
      <el-table-column prop="positionName" label="岗位" sortable></el-table-column>
      <el-table-column fixed="right" label="操作" width="180">
        <template slot-scope="scope" >
          <el-button  size="small" @click="handleUserEdit(scope.$index, scope.row)">职位变更</el-button>
          <el-button type="danger"  size="small" @click="handleDel(scope.$index, scope.row)">辞退</el-button>
        </template>
      </el-table-column>
    </el-table>

    <el-dialog title="新增信息" label-width="100px" :visible.sync="dialogFormVisible">
      <el-form :model="addUserForm" ref="addUserForm"  label-width="100px">
        <el-form-item label="部门"  prop="deptManager" :label-width="formLabelWidth">
          <el-select   v-model="addUserForm.deptId" filterable placeholder="部门" style="width: 400px" clearable prop="deptId" @change="clickPosition">
            <el-option  v-for="item in deptData"  :key="item.id" :label="item.name" :value="item.id.toString()" ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="岗位"  prop="deptManager" :label-width="formLabelWidth">
          <el-select   v-model="addUserForm.position" filterable placeholder="岗位" style="width: 400px" clearable prop="deptId">
            <el-option  v-for="item in positionData"  :key="item.id" :label="item.name" :value="item.id.toString()" ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="姓名" :label-width="formLabelWidth" prop="name">
          <el-input v-model="addUserForm.name"  style="width: 400px" auto-complete="off" ></el-input>
        </el-form-item>
        <el-form-item label="身份证" :label-width="formLabelWidth" prop="idCard">
          <el-input v-model="addUserForm.idCard"  style="width: 400px" auto-complete="off" ></el-input>
        </el-form-item>
        <el-form-item label="电话" :label-width="formLabelWidth" prop="idCard">
          <el-input v-model="addUserForm.phone"  style="width: 400px" auto-complete="off" ></el-input>
        </el-form-item>
        <el-form-item label="家庭住址" :label-width="formLabelWidth" prop="idCard">
          <el-input v-model="addUserForm.address"  style="width: 400px" auto-complete="off" ></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="addUser">确 定</el-button>
      </div>
    </el-dialog>

    <el-dialog title="职位变更" label-width="80px" :visible.sync="editUserVisible">
      <el-form :model="editUserForm" ref="editUserForm" >
        <el-form-item label="岗位名"  prop="deptManager" :label-width="formLabelWidth">
          <el-select   v-model="editUserForm.position" filterable placeholder="岗位" style="width: 400px" clearable prop="deptId">
            <el-option  v-for="item in positionData"  :key="item.id" :label="item.name" :value="item.id" ></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="editUserVisible = false">取 消</el-button>
        <el-button type="primary" @click="editUser">确 定</el-button>
      </div>
    </el-dialog>

    <!--<router-view/>-->
  </div>
</template>
<script>
  export default{
    mounted: function () {
      this.getUsers();
      this.getDept();
    },
    methods:{
      handleUserEdit(index, row){
        this.editUserVisible = true;
        this.editUserForm = Object.assign({}, row);
        this.getPosition(this.editUserForm.deptId)

      },
      getDept(){
        this.postRequest('/dept/condition').then((req)=>{
          this.deptData = req.data.data
        })
      },
      clickPosition(){
       this.getPosition(this.addUserForm.deptId)
      },
      getPosition(deptId){
        this.postRequest('/position/condition',{"deptId":deptId}).then((req) =>{
          this.positionData = req.data.data;
        })
      },
      getUsers(){
        this.postRequest('/user/condition',JSON.stringify(this.queryUserForm)).then((req)=>{
          this.tableData = req.data.data;

          this.tableData.push()
          this.$message({
            message:'查询成功',
            type:'success'
          })
        })
      },
      openAddUserDialog(){
        //todo查询职位信息
        //打开新增人员对话框
        this.dialogFormVisible=true;
      },
      addUser(){
        this.postRequest('/user', JSON.stringify(this.addUserForm)).then((req) => {
          this.tableData = req.data.data;
          this.dialogFormVisible = false;
          this.$message({
            message:'新增成功',
            type:'success',
          });
          this.getUsers();
        }).catch(() => {
          this.$message({
            message:'新增失败',
            type:'error',
          })
        })
      },
      editUser(){
        this.putRequest('/user', JSON.stringify(this.editUserForm)).then((req) =>{

          this.$message({
            message:'更新成功',
            type:'success',
          });
          this.getUsers();
          this.editUserVisible = false;
        }).catch(()=>{
          this.$message({
            message:'更新成功',
            type:'success',
          });
          this.getUsers();
          this.editUserVisible = false;
        })
      },
      handleDel(index, row){
        this.$confirm('确认辞退吗?', '提示', {
          type: 'warning'
        }).then(() => {
          let _this = this;
          //NProgress.start();
          this.deleteRequest('/user/'+row.id).then((res)=>{
            this.$message({
              message: '删除成功',
              type: 'success'
            });
            this.getUsers();
          }).catch(()=>{
            this.$message({
              message: '删除失败',
              type: 'error'
            });
          })
        })
      }
    },
    data(){
      return {
        dialogFormVisible:false,
        editUserVisible:false,
        formLabelWidth:'120px',
        queryUserForm:{
          name:'',
          position:'',
          deptId:'',
        },
        addUserForm:{
          position:'',
          name:'',
          idCard:'',
          phone:'',
          address:'',
          deptId:''

        },
        editUserForm:{
          userId:'',
          position:'',
          positionName:'',
          deptId:''
        },
        tableData:[],
        deptData:[],
        positionData:[],
      }
    }
  }
</script>
<style>
  .toolbar {
    background: #f2f2f2;
    padding: 10px;
  /*/ / border: 1 px solid #dfe6ec;*/
    margin: 10px 0px;
  }
</style>



